<template>
      <section class="haikuZone">

        <div class="haiku_content animated {{haiku.length!==0?'haiku_visible':'haiku_invisible'}}" v-show="haiku.length!==0">
            <p v-slicer="haiku[0]" transition="fade"></p>
            <p v-slicer="haiku[1]" transition="fade"></p>
            <p v-slicer="haiku[2]" transition="fade"></p>
            <p class="social_post">
                <a href="{{twitterURL}}" class="social_lnk social_lnk_twitter" title="poster sur twitter">PT</a>
                <a href="{{facebookURL}}" class="social_lnk social_lnk_fb"  title="poster sur facebook">PF</a>
            </p>

        </div>

        <div class="teapot_root">
            <object class="smoke" id="smoke" type="image/svg+xml"></object>
            <a class="btn-generate-haiku" title="générer un haiku" @click="generateHaiku()">
            <object class="teapot" id="teapot" type="image/svg+xml"></object>
            </a>
        </div>
    </section>
</template>

<script>
import Vivus from 'vivus'
// import slicer from 'chop-wrap'
import tea from '../assets/markov_tea.svg'
import smoke1 from '../assets/smoke1.svg'
import smoke2 from '../assets/smoke2.svg'
import smoke3 from '../assets/smoke3.svg'
import smoke4 from '../assets/smoke4.svg'
import smoke5 from '../assets/smoke5.svg'

const smokes = [smoke1, smoke2, smoke3, smoke4, smoke5]

function getSmoke () {
  return smokes[Math.floor(Math.random() * smokes.length)]
}

export default {
  data: function () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      vivusTeapot: null,
      vivusSmoke: null,
      twitterURLb: 'https://twitter.com/intent/tweet',
      facebookURLb: 'https://www.facebook.com/sharer/sharer.php'
    }
  },
  ready: function () {
    /* eslint-disable no-new */
    this.vivusTeapot = new Vivus('teapot', {type: 'oneByOne', start: 'autostart', duration: 100, file: tea})
    this.vivusSmoke = new Vivus('smoke', {type: 'oneByOne', start: 'manual', duration: 100, file: getSmoke()})
  },
  props: {
    haiku: Array
  },
  methods: {
    generateHaiku: function () {
      this.haiku = []
      this.$dispatch('generateHaiku')
      this.$nextTick(function () {
        this.vivusSmoke.reset().play()
        // console.log(this.$el.querySelector('.haiku_content'))
        this.$el.querySelector('.haiku_content').classList.add('tesste')
      })
    }
  },
  computed: {
    facebookURL: function () {
      // `this` points to the vm instance
      return this.facebookURLb + '?u=' + encodeURIComponent(window.location)
    },
    twitterURL: function () {
      const text = this.haiku.map((e) => { return e.join(' ') })
      return this.twitterURLb + '?text=' + encodeURIComponent(text.join('\n') + '\n') + '\&hashtags=haiku' + '\&url=' + encodeURIComponent(window.location)
    }
  },
  directives: {
    slicer: {
      update: function (newVal) {
        /* eslint-disable quotes*/
        if (newVal) {
          let cont =
           newVal.map((mot) => {
             return `<span class="wrap wrap-selected" aria-hidden="true">` + mot.toLowerCase() + `</span>`
           })
          .join(`<span class="wrap" aria-hidden="true"> </span>`)
        // this.el.innerHTML = cont.map((e) => { return `<p>` + e + `</p>` }).join('')
        // this.vm.$appendTo(this.el)
          this.el.innerHTML = cont
        }
      }
    }
  }
}
</script>

